
import React from 'react';
import './App.css';
// import Darkmode from './Component/Darkmode';
import Navbar from './Component/Navbar';
import TextForm from './Component/TextForm';
import Footer from './Component/footer';
import Alert from './Component/Alert';
import Contact from './Component/Contact';
import About from './Component/About';




import {
  BrowserRouter as Router,
  Switch,
  Route
} from "react-router-dom";





function App(){
    return (
//by using props i am giving heading of website
      <> 
      <Router>
      <Navbar title="Text Analyzer" home="Home" about="About Us" service="Services" contact="Contact US" /> 
      <Alert alerttxt=" try our services at once and if you have any issue just contact us."/>
    <div className="container">
     <Switch>
        <Route exact path="/About">
            <About />
          </Route>
          <Route exact path="/Contact">
            <Contact />
          </Route>
          <Route exact path="/">
          <TextForm heading="Add Heading :-" txtHeading="Add Notes Here:-"  />
          </Route>
        </Switch>
        </div>
        </Router>

        <Footer />
   
        </>
        
       
      
        
    );
}

export default App;





