import { useState } from "react";
import React from 'react';
import '../App.css';


export default function TextForm(props) {


    const Upclick = () => {
        console.log("Uppercase btn Was Clicked!");
        setText(Text.toUpperCase());
        setHead(Head.toUpperCase())
        document.title="Text UpperCase";
    }

    const Lowclick = () => {
        console.log("Lowercase Btn was Clicked");
        setText(Text.toLowerCase());
        setHead(Head.toLowerCase());
        document.title="Text LowerCase";
    }




    const reset = () => {
        console.log("reset btn clicked");
        setText("Enter Text here.....");
        setHead("eg.My Details..")
        alert("Text Has Reseted!");
        document.title="Text Reset";
    }

    const clearall = () => {
        setHead(' ');
        setText(' ');
        alert("All text Has Cleared!");
        document.title="Text cleared";
    }

    const close = () => {
        console.log("close btn Was Clicked");
        document.getElementById('container').style.display = "none";
        document.getElementById('showbtn').style.display = "block";
        document.getElementById('alert').style.display = "block";
        document.title="Text Closed";

    }

    const show = () => {
        console.log("show btn was clicked");
        document.getElementById('container').style.display = "block";
        document.getElementById('showbtn').style.display = "none";
        document.getElementById('alert').style.display = "none";
        document.title="Text Shown";
    }

    const Change = (event) => {
        console.log("Changing text");
        setText(event.target.value);
        document.title="Text Changing";
    }



    const Changehead = (event) => {
        console.log("Heading Changing");
        setHead(event.target.value);
        document.title="Text Changing";
    }




    const darkmode = () => {
        document.getElementById('body').style.backgroundColor = "black";
        document.getElementById('body').style.color = "#fff";

        document.body.style.backgroundColor = "black";
        document.body.style.color = "white";

        document.getElementById('textareaone').style.backgroundColor="black";
        document.getElementById('textareaone').style.color="white";
         
        document.getElementById('textarea').style.backgroundColor="black";
        document.getElementById('textarea').style.color="white";
      
        document.title="On Dark Mode";
        alert('Dark Mode Enabled');
    }



    const lightmode = () => {
        document.getElementById('body').style.backgroundColor = "#fff";
        document.getElementById('body').style.color = "black";

       
        document.body.style.backgroundColor = "white";
        document.body.style.color = "black";

        document.getElementById('textareaone').style.backgroundColor="white";
        document.getElementById('textareaone').style.color="black";
         
        document.getElementById('textarea').style.backgroundColor="white";
        document.getElementById('textarea').style.color="black";
      
        document.title="On Light Mode";
       
        alert('Light Mode Enabled')
    }




    const nightmode = () => {
        document.getElementById('body').style.backgroundColor = "rgb(252, 251, 228)";
        document.getElementById('body').style.color = "black";

       
        document.body.style.backgroundColor = "rgb(252, 251, 228)";
        document.body.style.color = "black";

        document.getElementById('textareaone').style.backgroundColor="rgb(210, 230, 220)";
        document.getElementById('textareaone').style.color="black";
         
        document.getElementById('textarea').style.backgroundColor="rgb(210, 230, 220)";
        document.getElementById('textarea').style.color="black";

        document.title="On Night Mode";
      
        alert('Night Mode Enabled')
    }



    const copy = () => {
        console.log("text copying");
        var Text = document.getElementById('textarea');
        Text.select();
        navigator.clipboard.writeText(Text.value);

        document.title="Text Copying";
    }



    const timer = () => {
        var date = new Date();
        document.getElementById("time").innerHTML = date.toLocaleDateString() + " " + date.toLocaleTimeString();
        setInterval(() => {
            timer();
        }, 1000);
    }



    const removespaces = () => {
        let newText = Text.split(/[ ]+/);
        console.log(newText);
        console.log("remove Spaces btn clicked");
        setText(newText.join(" "));

        document.title="Text Spaces Removed";

    }





    //   Setting up the placeholder of heading textarea and text textarea 
    // here the first parameter which is Text And Head in both methods That is nothing but the variables in which the input will be stored and by using SetText and SetHead we can update the text or head 
    const [Text, setText] = useState("Enter Text here.....");
    //   setText("Enter anything");
    const [Head, setHead] = useState("eg.My Details..")


    //returning the web page
    return (

        <>
            <div id="body">

                {/* //dark and light mode button is here */}
                <div class="form-check" id="darkbtn">
                    <input class="form-check-input" type="radio" name="exampleRadios" id="exampleRadios1" value="option1" onClick={darkmode} />
                    <label class="form-check-label" htmlFor="exampleRadios1" id="labelone">
                        Switch To Dark Mode
                    </label>
                </div>
                <div class="form-check" id="lightbtn">
                    <input class="form-check-input" type="radio" name="exampleRadios" id="exampleRadios2" value="option2" onClick={lightmode} checked />
                    <label class="form-check-label" htmlFor="exampleRadios2" id="labeltwo">
                        Switch To Light Mode
                    </label>
                </div>
                <div class="form-check" id="lightbtn">
                    <input class="form-check-input" type="radio" name="exampleRadios" id="exampleRadios3" value="option3" onClick={nightmode} />
                    <label class="form-check-label" htmlFor="exampleRadios3" id="labelthree">
                        Switch To Night Mode
                    </label>
                </div>

                {/* //Warning on the navbars below code is here */}
                <div className="container" id="alert">
                    <div class="alert alert-danger" role="alert">
                        You have Hidden the Notes Keeper <a href="../App.js" class="alert-link">For Again displaying</a>.Please Click On Show Button.
                    </div>
                </div>


                {/* //show button code is here */}
                <div className="container">
                    <div className="control">
                        <center><button type="btn" className="button is-link is-danger" onClick={show} id="showbtn">Show</button></center>
                    </div>
                </div>





                {/* //text anayzer code is here  */}
                <div id="container">
                    <div className="container">
                        <form>

                            <div className="field">
                                <center><h1 className="Apphead">Text Analyzing </h1></center>
                                <div className="control">
                                    <h1>{props.heading}</h1>
                                    <textarea className="textarea" rows="1" value={Head} onChange={Changehead} id="textareaone"></textarea>
                                </div>
                                <div className="control">
                                    <h1>{props.txtHeading}</h1>
                                    <textarea className="textarea" rows="10" value={Text} onChange={Change} id="textarea"></textarea>
                                </div>
                            </div>



                            <div className="field">
                                <div className="control" id="form_item">
                                    <button type="button" className="button is-dark" onMouseEnter={timer} id="time">Get Time</button>
                                </div>
                                <div className="control" id="form_item">
                                    <button type="button" className="button is-success" onClick={Upclick}>toUpperCase</button>
                                </div>
                                <div className="control" id="form_item">
                                    <button type="button" className="button is-success" onClick={Lowclick}>toLowerCase</button>
                                </div>

                                <div className="control" id="form_item">
                                    <button type="button" className="button is-link is-link" onClick={removespaces}>Remove Spaces</button>
                                </div>

                                <div className="control" id="form_item">
                                    <button type="button" className="button is-link is-link" onClick={copy}>Copy Text</button>
                                </div>

                                <div className="control" id="form_item">
                                    <button type="button" className="button is-link is-warning" onClick={clearall}>Clear all</button>
                                </div>

                                <div className="control" id="form_item">
                                    <button type="reset" className="button is-link is-danger" onClick={reset}>Reset</button>
                                </div>
                                <div className="control" id="form_item">
                                    <button type="reset" className="button is-link is-primary" onClick={close}>Hide</button>
                                </div>
                            </div>
                        </form>
                    </div>

                    <div className="container">
                        <h1>Your Heading Summary: -</h1>
                        <p>You Have Typed {Head.length}  Character and  {Head.split(" ").length} Words</p>

                        <h1>Your Text Summary: -</h1>
                        <p>You Have Typed {Text.length} Character and  {Text.split(" ").length}  Words</p>

                        <h1>Preview</h1>
                        <p className="previewHead">{Head}</p><hr />
                        <p className="preview">{Text}</p><hr />
                    </div>
                </div>

            </div>
        </>

    );
}








