import React from 'react'
import { useState } from 'react';

export default function Contact() {
 
  const txtchange = (event)=>{
    console.log("name IS Changing");
      setname(event.target.value);
  }

  const emailchange = (event)=>{
    console.log("email IS Changing");
      setemail(event.target.value);
  }

  const msgchange = (event)=>{
    console.log("massage IS Changing");
      setmassage(event.target.value);
  }

  const Clearall = ()=>{
    console.log("clearall btn clicked");
    setname(" ");
    setemail(" ");
    setmassage(" ");
  }


    const [name, setname] = useState("Enter Full Name");
  const [email, setemail] = useState("example@gmail.com");
  const [massage, setmassage] = useState("Enter Your Query");
    return (

               <>
<div id="container">


  <div className="container">
        <form action="./TextForm.js">
            <div className="field">
  <label className="label">Name</label>
  <div className="control">
    <input className="input is-success" type="text" value={name} onChange={txtchange}  />
  </div>
</div>



<div className="field">
  <label className="label">Email</label>
  <div className="control has-icons-left has-icons-right">
    <input className="input is-success" type="email" value={email} onChange={emailchange}  />
    <span className="icon is-small is-left">
      <i className="fas fa-envelope"></i>
    </span>
    <span className="icon is-small is-right">
      <i className="fas fa-exclamation-triangle"></i>
    </span>
</div>
</div>

<div className="field">
  <label className="label">Subject</label>
  <div className="control">
    <div className="select is-success">
      <select>
        <option>Text Converting</option>
        <option>Text analyzing issue</option>
        <option>Website issue</option>
        <option>Something Else</option>
      </select>
    </div>
  </div>
</div>

<div className="field">
  <label className="label">Message</label>
  <div className="control">
    <textarea className="textarea is-success" value={massage} onChange={msgchange} ></textarea>
  </div>
</div>


<div className="field is-grouped">
  <div className="control">
    <button type="submit" className="button is-success">Submit</button>
  </div>
  <div className="control">
    <button type="reset" className="button is-link is-danger" onClick={Clearall}>Clearall</button>
  </div>
</div>
</form>
</div>
</div>
        </>

    );
}
